@import url("https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css");

* {
  box-sizing: border-box;
}

/* GLOBAL STYLES */
@media only screen and (min-width:900px) {
  .title1 {
    display: block;
  }
  .title2 {
    display: none;
  }
  .app-sidebar-notes {
    height: calc(100vh - 78px);
    overflow-y: scroll;
  }
}

@media only screen and (max-width:900px) and (min-width: 500px) {
  .app-sidebar {
    width: 30%;
    height: 100vh;
    border-bottom: 1px solid #ddd;
    min-width: 210px;
  }
  .title1 {
    display: none;
  }
  .title2 {
    display: block;
  }  
  .app-sidebar-notes {
    height: calc(100vh - 78px);
    overflow-y: scroll;
  }
}

@media only screen and (max-width:500px) and (min-width: 260px){
  .title1 {
    display: block !important;
  }
  .title2 {
    display: none !important;
  }
  .App {
    display: flex;
    flex-direction: column;
    
  }
  #sidebar_body{
    display: none;
  }
  .app-sidebar {
    min-width: 100%;
    max-height: 225px;
    border-bottom: 1px solid black;
  }
  .title1 {
    display: none;
  }
  .title2 {
    display: block;
  }
  .app-sidebar-notes {
    max-height: 138px;
    min-width: 100%;
    overflow-y: scroll;
  }
  .app-main {
    min-width: 100%;
  }
  .no-active-note{
    width: 100% !important;
    text-align: center;
  }
}
@media only screen and (max-width:260px) {
  .title1 {
    display: none !important;
  }
  .title2 {
    display: block !important;
  }
  .App {
    display: flex;
    flex-direction: column;
    
  }
  #sidebar_body{
    display: none;
  }
  .app-sidebar {
    min-width: 234px !important;
    width: 100% !important;
    max-height: 225px;
    border-bottom: 1px solid black;
  }
  .title1 {
    display: none;
  }
  .title2 {
    display: block;
  }
  .app-sidebar-notes {
    max-height: 138px;
    overflow-y: scroll;
  }
  .app-main {
    width: 100% !important;
    min-width: 234px !important;
  }
  .no-active-note{
    width: 100% !important;
    min-width: 234px !important;
    text-align: center;
  }
}

body,
.App {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  font-size: 16px;
  background-color:aliceblue
}

button {
  background: none;
  border: 0;
  margin: 0;
  padding: 0;
  font-size: inherit;
  font-family: inherit;
  cursor: pointer;
  color: #08c;
}
button:hover {
  color: #04c;
}

.App {
  display: flex;
}

/* SIDEBAR STYLES */

.app-sidebar {
  width: 30%;
  height: 100vh;
  border-right: 1px solid #ddd;
}

.app-sidebar-header {
  display: flex;
  justify-content: space-between;
  padding: 25px;
}
.app-sidebar-header h1 {
  margin: 0;
}


.app-sidebar-note {
  padding: 25px;
  cursor: pointer;
}
.sidebar-note-title {
  display: flex;
  justify-content: space-between;
}
.app-sidebar-note button {
  color: crimson;
}
.app-sidebar-note p {
  margin: 10px 0;
}
.app-sidebar-note small {
  display: block;
  color: #999;
}

.app-sidebar-note:hover {
  background: #ddd;
}
.app-sidebar-note.active,
.app-sidebar-note.active small {
  background: #08c;
  color: white;
}

/* NOTE EDITOR/PREVIEW (MAIN) STYLES */

.app-main {
  width: 70%;
  height: 100vh;
}
.app-main-note-edit {
  height: 90vh;
}

.no-active-note {
  width: 70%;
  height: 100vh;
  line-height: 100vh;
  text-align: center;
  font-size: 2rem;
  color: #999;
}

/* Editing */
.app-main-note-edit {
  padding: 25px;
}

.app-main-note-edit input,
textarea {
  display: block;
  border: 1px solid #ddd;
  margin-bottom: 20px;
  width: 100%;
  height: calc(90vh - 80px);
  padding: 10px;
  resize: none;
  font-size: inherit;
  font-family: inherit;
}
.app-main-note-edit input {
  height: 50px;
  font-size: 2rem;
}

/* Preview */
.app-main-note-preview {
  border-top: 1px solid #ddd;
  overflow-y: scroll;
  background: rgba(0, 0, 0, 0.02);
}

.preview-title {
  padding: 25px 25px 0 25px;
  margin: 0;
}

.markdown-preview {
  padding: 0 25px 25px 25px;
  font-size: 1rem;
  line-height: 2rem;
}